"use client";

import { useEffect, useState } from "react";
import { getUsers, toggleUserActivation } from "@/lib/actions";
import {
    Table,
    TableBody,
    TableCell,
    TableHead,
    TableHeader,
    TableRow,
} from "@/components/ui/table";
import { Card, CardContent } from "@/components/ui/card";
import { Switch } from "@/components/ui/switch";
import { Avatar, AvatarFallback, AvatarImage } from "@/components/ui/avatar";
import { Badge } from "@/components/ui/badge";

export default function UsersManagement() {
    const [users, setUsers] = useState<any[]>([]);
    const [loading, setLoading] = useState(true);

    useEffect(() => {
        fetchUsers();
    }, []);

    async function fetchUsers() {
        const data = await getUsers();
        setUsers(data);
        setLoading(false);
    }

    async function handleToggle(id: string) {
        await toggleUserActivation(id);
        fetchUsers(); // Refresh
    }

    return (
        <div className="space-y-6">
            <header className="flex justify-between items-end">
                <div>
                    <h1 className="text-3xl font-black tracking-tight uppercase">User Management</h1>
                    <p className="text-muted-foreground">Activate or deactivate staff and manage permissions</p>
                </div>
            </header>

            <Card className="border-border shadow-md">
                <CardContent className="p-0">
                    <Table>
                        <TableHeader>
                            <TableRow className="bg-muted/50 transition-none hover:bg-muted/50">
                                <TableHead className="w-[300px]">USER</TableHead>
                                <TableHead>ROLE</TableHead>
                                <TableHead>STATUS</TableHead>
                                <TableHead className="text-right">ACTIONS</TableHead>
                            </TableRow>
                        </TableHeader>
                        <TableBody>
                            {users.length === 0 && !loading ? (
                                <TableRow>
                                    <TableCell colSpan={4} className="text-center h-24 text-muted-foreground">
                                        No users found
                                    </TableCell>
                                </TableRow>
                            ) : null}
                            {users.map((user: any) => (
                                <TableRow key={user.id} className="hover:bg-muted/30 transition-colors">
                                    <TableCell>
                                        <div className="flex items-center gap-3">
                                            <Avatar className="h-9 w-9 border border-border">
                                                <AvatarImage src={`https://i.pravatar.cc/150?u=${user.id}`} />
                                                <AvatarFallback className="bg-primary/10 text-primary font-bold text-xs">
                                                    {user.name?.charAt(0)}
                                                </AvatarFallback>
                                            </Avatar>
                                            <div className="flex flex-col">
                                                <span className="font-bold text-sm tracking-tight leading-none mb-1">{user.name}</span>
                                                <span className="text-[11px] text-muted-foreground font-mono">{user.email}</span>
                                            </div>
                                        </div>
                                    </TableCell>
                                    <TableCell>
                                        <Badge variant={user.role === "ADMIN" ? "default" : "secondary"} className="font-bold text-[10px] tracking-widest px-1.5 py-0">
                                            {user.role}
                                        </Badge>
                                    </TableCell>
                                    <TableCell>
                                        <div className="flex items-center gap-2">
                                            <div className={`h-1.5 w-1.5 rounded-full ${user.isActive ? "bg-emerald-500 shadow-[0_0_8px_rgba(16,185,129,0.5)]" : "bg-amber-500 shadow-[0_0_8px_rgba(245,158,11,0.5)]"}`} />
                                            <span className="text-xs font-semibold tracking-tight uppercase">
                                                {user.isActive ? "Active" : "Pending"}
                                            </span>
                                        </div>
                                    </TableCell>
                                    <TableCell className="text-right">
                                        <div className="flex justify-end pr-2">
                                            <Switch
                                                checked={user.isActive}
                                                onCheckedChange={() => handleToggle(user.id)}
                                            />
                                        </div>
                                    </TableCell>
                                </TableRow>
                            ))}
                        </TableBody>
                    </Table>
                </CardContent>
            </Card>
        </div>
    );
}
